/* Global Styles */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}
.main-container {
  padding: 0 17rem;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.navbar-items {
  display: flex;
}
.navbar-items li {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  padding-inline: 18px;
  display: flex;
  align-items: center;
}

.profile{
  position: relative;
}
.hide-menu{
  display: none;
}
.show-menu{
  display: block;
  position: absolute;
  top: 23px;
  left: -95px;
  z-index: 1;
  background: transparent url('./assets//profile-menu.svg') 0% 0% no-repeat padding-box;
  width: 173px;
height: 139px;
}

.lang-box {
  border: 1px solid #c2d5e5;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 10px;
}

/* Hero section */
.hero-section {
  background-color: #e6f0f9;
  position: relative;
  width: 100wh;
}

.search-container {
  padding: 140px 0;
  display: grid;
  row-gap: 20px;
}
.search-container h2 {
  font-family: "Poppins", sans-serif;
  font-size: 46px;
  width: 453px;
  word-wrap: break-word;
}

label {
  position: relative;
}

label:before {
  content: "";
  position: absolute;
  left: 10px;
  top: 0;
  bottom: 0;
  width: 20px;
  background: url("/src/assets/location-icon.svg") center / contain no-repeat;
}

input {
  padding: 10px 30px;
}

.search-tag {
  color: #0969c3;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
  display: flex;
  margin: 12px 0px 0px -12px;
}
.search-tag li {
  background: #cfe3f4 0% 0% no-repeat padding-box;
  border-radius: 6px;
  padding: 8px 14px;
  margin: 0 6px;
}

#hero-image {
  position: absolute;
  width: 900px;
  right: 0px;
  bottom: -58px;
}

/* Card styles */
.category-section{
  margin: 40px 0 90px 0;
}
.category-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.category-header span{
  display: flex;
  align-items: center;
  color: #0969C3;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
}

.category-header span:hover{
  color:#002745;
  cursor: pointer;
}

.main-container h3{
  margin-top: 30px;
  padding:  0px 30px 30px 0;
  font-size: 30px;
  font-family: "Poppins", sans-serif;
}
.box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, 20rem);
  column-gap: 20px;
  row-gap: 20px;
}
.box-header h6{
  font-size: 14px;
}
.card{
  background: #FFFFFF 0% 0% no-repeat padding-box;
border: 1px solid #E2DEDE;
border-radius: 10px;
padding: 24px;
display: flex;
align-items: center;
cursor: pointer;

}
.card-color{
  background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 6px 24px #0969C30D;
border: 1px solid #0969C3;
}
.card-image{
  margin-right: 20px;
}


.job-section{
  background: #E6F0F9 0% 0% no-repeat padding-box;
  padding-top: 90px ;
  padding-bottom: 99px ;
}
.box-body li{
  list-style-type: disc;
  font-family: "Poppins", sans-serif;
  color: #999999;
  font-size: 12px;
  margin-left: 17px ;
}

.job-box{
  padding: 20px;
  border-radius: 6px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  height: 180px;
  transition: height 0.5s;
  overflow: hidden;
  cursor: pointer;
}
.job-box:hover{
  height: 230px;
}
.box-header{
  display: flex;
  position: relative;
}
button{
  background: #0969C3 0% 0% no-repeat padding-box;
  border-radius: 4px; 
  border: none;
  padding: 11px 16px;
  display: flex;
  align-items: center;
  color:white;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  margin-top: 30px;
  cursor: pointer;
}

#bookmark{
  position: absolute;
  right: 0;
}
.box-header h6{
  width: 186px;
  word-wrap: break-word;
}
.body-header{
  display: flex;
  justify-content: space-between;
  
}
.body-header h6{
  font-size: 12px;
  font-family: "Poppins", sans-serif;
}
.body-header span{
  font-family: "Poppins", sans-serif;
  color: #999999;
  font-size: 12px;
}

/* Job Section */
.company-img{
  margin:0 12px 12px 0
}

/* Partners section */
.partners-section{
  padding-top: 90px;
  padding-bottom: 90px;
  text-align: center;
}
.partners-section h5{
  font-family: "Poppins", sans-serif;
  margin-bottom: 33px;
}
.partner-logo{
  padding-bottom: 88px;
}

/* Footers sectioin */
.footer-grid{
  display: flex;
  justify-content: space-between;
  font-family: "Poppins", sans-serif;
  color: #999999;
  font-size: 12px;
}
.individual-section{
  display: block;
}
.social-links span{
  display: flex;
  align-items: center;
}
.social-links img{
  margin-left: 5px;
}
.footer-section p{
  text-align: center;
  font-family: "Poppins", sans-serif;
  color: #999999;
  font-size: 12px;
  margin-bottom: 30px;
}
hr{
  /* color: #999999; */
  height: 1px;
        background-color: #e7e7e7;
        border: none;
        margin: 30px 0
}